<template>
	<div id="index">
		<div class="header">
			<van-row gutter="20" style="margin-right: 0px !important;margin-left: 0px !important;">
        <van-col span="4" style="padding-right: 0px !important;padding-left: 0px;" @click.native="show1 = true">
          <img src="static/img/icon_dingwei_sy@2x.png" style="width: 10px;margin-top: -4px">
          <!--<span class="font_12" style="position: relative;color: #8b8b8b;">-->
            <div class="font_12" style="position: relative;top: -18px;color: rgb(139, 139, 139);line-height: 12px;">
            {{cityName}}
          </div>
        </van-col>
				<van-col span="16" class='search font_12' style="background:#ffffff;">
          <van-search v-model="value" placeholder="搜索" @search="getGoodsList1"/>
        </van-col>
        <van-col span="3" style="padding-left:0px !important;padding-right: 0px;">
          <img src="static/img/icon_fx_sy@2x.png" style="vertical-align: middle;width: 20px;" @click="copeText"/>
        </van-col>
			</van-row>
		</div>
    <div class="van-modal" style="z-index: 2008;" v-show="show" @click="show=false">
      <div style="background-color: #ffffff;text-align: center;margin-top: 50%;width: 90%;margin-left: 5%;border-radius: 10px;padding: 10px 0;">
        <img src="static/img/img_fx_fxersf.png" style="height: 36px;"/>
        <br>
        <span class="color_c10804">已经复制为链接,快去粘贴分享吧!</span>
      </div>
    </div>
		<div class="swiperTwo">
      <van-swipe :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(image, index) in images" :key="index">
          <img :src="image.bannerimg" width="100%" height="150px" @click="toGoods(image)"/>
        </van-swipe-item>
      </van-swipe>
      <!--<van-swipe :autoplay="3000" :show-indicators="true">-->
        <!--<van-swipe-item v-for="(image, index) in images" :key="index">-->
          <!--<img :src="image.bannerimg" width="100%" height="150px" style="display: block;" @click="toGoods(image)"/>-->
        <!--</van-swipe-item>-->
      <!--</van-swipe>-->
		</div>
    <div class="typeList">
      <van-row>
        <van-col class="width_25">
          <img class="typeImg" src="static/img/icon_jiancaisy.png" @click="toGoodsClassify(1)"/>
          <span class="font_12">建材</span>
        </van-col>
        <van-col class="width_25">
          <img class="typeImg" src="static/img/icon_fcsfs.png" @click="toGoodsClassify(2)"/>
          <span class="font_12">家居</span>
        </van-col>
        <van-col class="width_25">
          <img class="typeImg" src="static/img/img111.png" @click="toGoodsClassify(3)"/>
          <span class="font_12">装修设计</span>
        </van-col>
        <van-col class="width_25">
          <img class="typeImg" src="static/img/nav——tcfwsy.png" @click="toCityWide()"/>
          <span class="font_12">同城服务</span>
        </van-col>
      </van-row>
    </div>
    <div class="background_color_ffffff font_12 margin_top_10" style="border-radius: 5px" @click="toTimeOfSecKill">
      <div style="padding: 5px 8px;display: inline-block;width: 20%;height: 40px;line-height: 20px;">
        <div>
          <img src="static/img/xsms111.png">
        </div>
        <div class="color_c10804 font_14">8:00-24:00</div>
      </div>
      <div class="color_8b8b8b" style="padding: 5px 8px;display: inline-block;width: 44%;height: 40px;line-height: 20px;">
        <van-swipe :autoplay="2000" vertical style="height: 40px;" :height="40" :show-indicators="false">
          <van-swipe-item v-for="(item, index) in titleList" :key="index" :height="40" indicator-color="white" style="line-height: 40px">
            <div class="font_16">{{item.content}}</div>
          </van-swipe-item>
        </van-swipe>
      </div>
      <div style="padding: 5px 8px;display: inline-block;width: 20%;height: 40px;line-height: 20px;" class="text_center">
        <img src="static/img/djs111.png">
        <div class="text_center color_c10804" v-if="!dhms">{{h}}:{{m}}:{{s}}</div>
        <div class="text_center color_c10804" v-if="dhms">正在进行中</div>
      </div>
    </div>
    <div style="width: 100%;margin-bottom: 44px;">
      <div style="text-align: center;padding-top: 10px;">
        <img src="static/img/img_sptj_sy.png" style="width: 20%">
      </div>
      <div class="goods-list">
        <van-row gutter="10">
          <van-col span="12" v-for="(item, index) in goodsList" @click.native="toGoods(item)">
            <van-panel style="border-radius: 3px;overflow: hidden;box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);">
              <div slot="header">
                <img :src="item.goodslogo.split(',')[0]" v-lazy="item.goodslogo.split(',')[0]" :loading="item.goodslogo.split(',')[0]" width="100%" style="display: block;" />
              </div>
              <div class="goods_content">
                <div class="goods_title" style="max-height: 48px;">
                  <span style="font-size: 14px">{{item.goodsname}}</span>
                  <br>
                  <span style="font-size: 12px;color:#8b8b8b;">{{item.introduce}}</span>
                </div>
                <div class="goods_footer">
                  ￥<span class="goods_price color_c10804" style="font-size: 12px" v-if="item.type==1">{{item.price}}</span>
                  <span class="goods_price color_c10804" style="font-size: 12px" v-if="item.type==2">{{item.spike}}</span>
                  <span style="font-size: 12px;color:#8b8b8b;float: right">{{item.paynum}}人付款</span>
                </div>
              </div>
            </van-panel>
          </van-col>
        </van-row>
      </div>
    </div>
    <van-actionsheet
      v-model="show1"
      :actions="actions"
      @select="onSelect"
      :close-on-click-overlay=false
    />
	</div>
</template>

<script>
	export default {
		data() {
			return {
        show1:false,
        actions: [],
        goodsList:[],
        value:'',
        // cityName:'南京',
        show:false,
        goodsList: [],
        images: [{adimg:'static/img/banner@2x.png'}],
        classify: [],
        cityId:localStorage.getItem('cityId'),
        userId:localStorage.getItem('userId'),
        cityName:localStorage.getItem('cityName'),
        titleList:[],
        d:'',
        h:'',
        m:'',
        s:'',
        dhms:false,
			}
		},
		mounted() {
      // this.getCity();
      // this.getGoodsList();
      this.getTitleList();
      this.countTime();
      this.getCityList();
      if(localStorage.getItem('cityId')){
        this.show1 = false;
        this.getImages();
      }else {
        this.show1 = true;
      }
		},
		methods: {
      onSelect(item) {
        this.show1 = false;
        this.cityName = item.cityname;
        this.cityId = item.cityid;
        localStorage.setItem("cityName",item.cityname);
        localStorage.setItem("cityId",item.cityid);
        this.getImages();
        this.getGoodsList();
      },
      getCityList(){
        let $this = this;
        let obj = {
          userid: $this.userId,
        };
        let qs = require('qs');
        $this.$ajax.post("city/getcitys", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            for(let i = 0;i<rpdata.data.data.length;i++){
              rpdata.data.data[i].name = rpdata.data.data[i].cityname;
              rpdata.data.data[i].id = rpdata.data.data[i].cityid;
            }
            $this.actions = rpdata.data.data;
            $this.getGoodsList();
          }
        })
        .catch(function(err) {})
      },
      countTime: function () {
        //获取当前时间
        var date = new Date();
        var now = date.getTime();
        //设置截止时间
        var endDate = new Date(new Date(new Date().toLocaleDateString()).getTime() + 8 * 60 * 60 * 1000 - 1);
        // var endDate = new Date("23:59:59");
        var end = endDate.getTime();
        //时间差
        var leftTime = end - now;
        //定义变量 d,h,m,s保存倒计时的时间
        if (leftTime >= 0) {
          this.d = Math.floor(leftTime / 1000 / 60 / 60 / 24);
          this.h = Math.floor(leftTime / 1000 / 60 / 60 % 24);
          this.m = Math.floor(leftTime / 1000 / 60 % 60);
          this.s = Math.floor(leftTime / 1000 % 60);
        } else {
          this.dhms = true;
        }
        //递归每秒调用countTime方法，显示动态时间效果
        setTimeout(this.countTime, 1000);
      },
      toGoods(dataMap){
        if(dataMap.type==1){
          this.$store.commit('setGoodsInfo', dataMap);
          this.$router.push('goods');
        }else{
          this.$store.commit('setGoodsInfo', dataMap);
          this.$router.push('goodsKill');
        }
      },
      toTimeOfSecKill(){
        if(this.dhms){
          this.$router.push('timeOfSecKill');
        }else{
          this.$toast({
            message: '商品秒杀活动8:00开始',
            position: "bottom"
          });
        }
      },
      getTitleList(){
        let $this = this;
        let obj = {
          userid: $this.userId,
        };
        let qs = require('qs');
        $this.$ajax.post("title/gettitlelist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.titleList = rpdata.data.data;
          }
        })
        .catch(function(err) {})
      },
      toCityWide(){
        this.$router.push('cityWide');
      },
      toGoodsClassify(dataMap){
        let goodsClassify = {
           id:dataMap,
           index:0
        };
        this.$store.commit('setGoodsClassify', goodsClassify);
        this.$router.push('goodsClassify');
      },
      toGPS(){
        this.$router.push('GPS');
      },
      getGoodsList1(){
        this.$router.push({path:'goodsSearch',query:{value:this.value}});
      },
      getGoodsList(){
        this.goodsList = [];
        let $this = this;
        let obj = {
          userid: $this.userId,
          goodsname: '',
          cityid: localStorage.getItem('cityId')
        };
        let qs = require('qs');
        $this.$ajax.post("goods/gettuijianlist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.goodsList = rpdata.data.data;
          }
        })
        .catch(function(err) {})
      },
      getCity(){
        let $this = this;
        let obj = {
          userid: $this.userId,
          city : '徐州'
        };
        let qs = require('qs');
        $this.$ajax.post("user/changeaddress", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            localStorage.setItem('cityId',rpdata.data.data.cityid);
            $this.cityId = rpdata.data.data.cityid;
            $this.getImages();
          }else{
            console.log('没有什么能够阻挡')
          }
        })
        .catch(function(err) {})
      },
      getImages(){
        let $this = this;
        let obj = {
          userid: $this.userId,
          cityid : $this.cityId
        };
        let qs = require('qs');
        $this.$ajax.post("banner/getbannerlist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.images = rpdata.data.data;
          }
        })
        .catch(function(err) {})
      },
		  copeText(){
        dsBridge.call('shareUrl','https://www.pgyer.com/DRwB');
      }
		}
	}
</script>

<style>
  #index .van-search input {
    color: #8b8b8b;
  }
  #index .typeList {
    text-align: center;
  }
  #index .typeList span {
    color: rgb(102, 102, 102)
  }
  #index .typeImg {
    display: block;
    margin: 10px auto;
  }
	#index {
		width: 100%;
	}
	#index .header {
		height: 44px;
		line-height: 44px;
		box-shadow: 0px 2px 10px lightgrey;
		overflow: hidden;
		position: fixed;
		top: 0;
		width: 100%;
		z-index: 99;
		background-color: white;
	}
	#index .header img {
		vertical-align: text-top;
	}
	#index .search {
		border: 1px solid lightgray;
		border-radius: 30px;
		margin-top: 6px;
	}
	#index .header .van-col {
		text-align: center;
	}
	#index .van-search {
    padding: 0px;
  }
	#index .van-search {
		background-color: transparent!important;
	}
	#index .swiper img {
		margin: 0 auto;
		display: block;
	}
	#index .nav img {
		width: 100%;
		height: auto;
	}
	#index .navCon span {
		font-size: 13px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		color: white;
	}
	#index .rightText div {
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-size: 16px;
	}
	#index .footer .van-col div {
		text-align: center;
		font-size: 14px;
	}
	#index .swiperTwo{
    margin-top: 44px;
	}
  #index .van-field__control {
    font-size: 12px;
  }
</style>



